import Typograpy from "component/molecules/Typograpy";
import { useAnimation, motion } from "framer-motion";
import React from "react";
import { InView } from "react-intersection-observer";
import { colors } from "theme/colors";
import { Box, FlexBox } from "theme/globalStyle";

export const ContentsArea = () => {
  const controls = useAnimation();

  const contentsFontSize = { pc: 2, mobile: 1.6 };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      // 
      opacity: 1,
      transition: {
        staggerChildren: 0.6, //      
      },
    },
  };

  const itemVariants = {
    //  motion 
    hidden: { opacity: 0, y: 50 }, // y 50
    visible: {
      opacity: 1,
      y: 0, //  
      transition: {
        duration: 0.9,
      },
    },
  };

  function photoInview(inview: boolean) {
    if (inview) {
      controls.start("visible");
    }
  }
  return (
    <InView
      as="div"
      onChange={photoInview}
      threshold={0.2} //      inView
      style={{ width: "100%", height: "100%" }}
    >
      <FlexBox
        $isFullWidth
        $height={"100%"}
        $jc={"center"}
        $gap={{ pc: 6, mobile: 3 }}
        $ai={"center"}
      >
        <FlexBox
          $py={1.2}
          $px={1.2}
          // $btw={1}
          $bbw={1}
          $bbc={colors.DEFAULT_BORDER_COLOR}
          $btc={colors.DEFAULT_BORDER_COLOR}
        >
          <Typograpy
            color={"#ab8e6b"}
            fontWeight="Eulyoo-SemiBold"
            fontSize={contentsFontSize}
          >
             
          </Typograpy>
        </FlexBox>
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate={controls}
          style={{ width: "100%" }}
        >
          <FlexBox $isFullWidth $gap={2.8} $ai={"center"}>
            <motion.div variants={itemVariants}>
              <FlexBox $gap={0.4} $ai={"center"}>
                <Typograpy fontSize={contentsFontSize}>
                   
                </Typograpy>
                <Typograpy fontSize={contentsFontSize}>
                     .
                </Typograpy>
              </FlexBox>
            </motion.div>
            <motion.div variants={itemVariants}>
              <FlexBox $gap={0.4} $ai={"center"}>
                <Typograpy fontSize={contentsFontSize}>
                     
                </Typograpy>
                <Typograpy fontSize={contentsFontSize}>
                  ,  
                </Typograpy>
                <Typograpy fontSize={contentsFontSize}>
                   .
                </Typograpy>
              </FlexBox>
            </motion.div>
            <motion.div variants={itemVariants}>
              <FlexBox $gap={0.4} $ai={"center"}>
                <Typograpy fontSize={contentsFontSize}>
                     
                </Typograpy>
                <Typograpy fontSize={contentsFontSize}>
                    .
                </Typograpy>
              </FlexBox>
            </motion.div>
          </FlexBox>
        </motion.div>
        <FlexBox $gap={0.8}>
          <FlexBox
            $flexDirection="row"
            $ai={"flex-end"}
            $gap={1.2}
            $isFullWidth
          >
            <Typograpy fontWeight="Eulyoo-SemiBold" fontSize={contentsFontSize}>
                
            </Typograpy>
            <Box $width={4}>
              <Typograpy fontSize={{ pc: 1.4, mobile: 1.2 }}> </Typograpy>
            </Box>
            <Typograpy
              fontWeight="Eulyoo-SemiBold"
              fontSize={contentsFontSize}
              color={"#ab8e6b"}
            >
               
            </Typograpy>
          </FlexBox>
          <FlexBox
            $flexDirection="row"
            $ai={"flex-end"}
            $gap={1.2}
            $isFullWidth
          >
            <Typograpy fontWeight="Eulyoo-SemiBold" fontSize={contentsFontSize}>
                
            </Typograpy>
            <FlexBox $width={4}>
              <Typograpy fontSize={{ pc: 1.4, mobile: 1.2 }}> </Typograpy>
            </FlexBox>
            <Typograpy
              fontWeight="Eulyoo-SemiBold"
              fontSize={contentsFontSize}
              color={"#ab8e6b"}
            >
               
            </Typograpy>
          </FlexBox>
        </FlexBox>
      </FlexBox>
    </InView>
  );
};
